.main {
  margin-top: 2.375rem;
}

.main .section1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.booksConta {
  background: #fff;
  width: 85%;
  height: 10.625rem;
  margin-bottom: 0.938rem;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.book {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
}

.book p {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
  font-weight: bold;
  color: #121212;
  margin-bottom: 0.25rem;
}

.book h2 {
  font-family: 'Roboto', sans-serif;
  font-size: 1.375rem;
  font-weight: bold;
  letter-spacing: -0.2px;
  color: #121212;
}

.book h3 {
  margin: 0.25rem 0 0;
  font-family: 'Roboto', sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  color: #4386bf;
}

.funcButton {
  display: flex;
  margin-top: 1.313rem;
}

.funcButton button {
  width: 4.563rem;
  height: 1.188rem;
  margin: 0.188rem 0.938rem 0.125rem 0;
  font-family: 'Roboto', sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  color: #4386bf;
  background: none;
  border: none;
  margin-right: 1.063rem;
  cursor: pointer;
}

.Line-2 {
  background: #e8e8e8;
  width: 0.125rem;
  height: 1.5rem;
  margin-right: 1.063rem;
}

/* Progress */

.progress {
  display: flex;
  margin-right: 6rem;
  align-items: center;
}

.circle-wrap {
  width: 68px;
  height: 68px;
  background: #fefcff;
  border-radius: 50%;
  border: 1px solid #cdcbd0;
}

.mask.full,
.circle .fill {
  animation: fill ease-in-out 3s;
  transform: rotate(135deg);
}

.mask .fill {
  clip: rect(0, 35px, 68px, 0);
  background-color: #307bbe;
}

.circle-wrap .circle .mask,
.circle-wrap .circle .fill {
  width: 68px;
  height: 68px;
  position: absolute;
  border-radius: 50%;
}

.circle-wrap .circle .mask {
  clip: rect(0, 68px, 68px, 34px);
}

@keyframes fill {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(135deg);
  }
}

.circle-wrap .inside-circle {
  width: 58px;
  height: 58px;
  border-radius: 50%;
  background: #fff;
  margin-top: 5px;
  margin-left: 5px;
  position: absolute;
  z-index: 100;
}

.prcg {
  margin-left: 1.3rem;
}

.prcg h5 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  font-weight: normal;
  color: #121212;
}

.prcg h6 {
  opacity: 0.5;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.875rem;
  font-weight: normal;
  color: #121212;
}

.Line-3 {
  border: solid 1px #e8e8e8;
  background-color: #e8e8e8;
  width: 0.05rem;
  height: 4.375rem;
  margin-left: 4.938rem;
  margin-right: 3.688rem;
}

.chapter h3 {
  opacity: 0.5;
  font-family: 'Roboto', sans-serif;
  font-size: 0.813rem;
  font-weight: 300;
  font-stretch: normal;
  color: #121212;
}

.chapter h4 {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: -0.4px;
  color: #121212;
  margin-top: 0.438rem;
  margin-bottom: 1.438rem;
}

.chapter button {
  font-family: 'Roboto', sans-serif;
  font-size: 0.813rem;
  font-weight: 300;
  letter-spacing: 0.5px;
  text-align: center;
  padding: 0.438rem 1.188rem 0.5rem 1.375rem;
  color: #e8e8e8;
  background-color: #0290ff;
  border: none;
  cursor: pointer;
}
